// iPhone X
$device-silver: #ebebe3;
$device-silver-dark: darken($device-silver, 10%);
$device-silver-panel: #222;
$height: 55vh;
$width: 16.5vw;

.device,
.device *,
.device ::after,
.device ::before,
.device::after,
.device::before {
  box-sizing: border-box;
}
.device {
  position: relative;
  transform: scale(1);
  z-index: 1;
}
.device .device-frame {
  z-index: 1;
}
.device .device-content {
  background-color: #000;
  background-position: center center;
  background-size: cover;
  object-fit: cover;
  position: relative;
}
.device-iphone-x {
  height: $height;
  width: $width;
  line-height: normal;

  .device-frame {
    background: $device-silver-panel;
    border-radius: 48px;
    box-shadow: inset 0 0 2px 2px $device-silver-dark,
      inset 0 0 0 7px $device-silver;
    height: $height;
    padding: 1.4vh;
    width: $width;
  }
  .device-content {
    border-radius: 32px;
    height: $height - 2.8vh;
    width: $width - 1.55vw;
  }
  .device-top {
    width: 100%;
    display: block;
  }
  .device-top-left {
    float: left;
    display: inline;
    padding: 0.5vh;
    padding-left: 1.5vw;
  }

  .device-top-right {
    float: right;
    padding: 0.5vh;
    padding-right: 0.8vw;
  }

  .device-body {
    height: $height - 16.5vh;
    margin-top: 2vh;
  }
  .device-body-bottom-menu {
    position: absolute;
    width: 100%;
    bottom: 0.8vh;
  }
  .menu-div {
    text-align: center;
    font-size: 1vh;
  }

  .menu-div:nth-child(n + 5) {
    margin-top: 2vh;
  }
  .menu-icon {
    height: 5vh;
    width: 5vh;
    opacity: 1;
  }
  .menu-icon:hover {
    opacity: 0.8;
  }

  .device-body-bottom {
    background-color: rgba(0, 0, 0, 0.4);
    border-radius: 25px;
    padding: 1vh;
    margin: 0 1vh;
  }
  .device-btns-home {
    position: absolute;
    bottom: 0.8vh;
    left: 4.5vw;
    height: 0.4vh;
    width: 8vw;
    background-color: white;
    margin: 1px auto;
    border-radius: 50px;
    opacity: 1;
  }
  .device-btns-home:hover {
    opacity: 0.8;
  }

  .device-stripe {
    pointer-events: none;
    &::after,
    &::before {
      border: solid rgba(#333, 0.25);
      border-width: 0 7px;
      content: "";
      height: 7px;
      left: 0;
      position: absolute;
      width: 100%;
      z-index: 9;
    }
    &::after {
      top: 85px;
    }
    &::before {
      bottom: 85px;
    }
  }

  .device-header {
    background: $device-silver-panel;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
    height: 3vh;
    left: 50%;
    margin-left: -22.5%;
    position: absolute;
    top: 1.1vh;
    width: 7.5vw;

    &::after,
    &::before {
      content: "";
      height: 1vh;
      position: absolute;
      top: 0;
      width: 1vw;
    }
    &::after {
      background: radial-gradient(
        circle at bottom left,
        transparent 0,
        transparent 75%,
        $device-silver-panel 75%,
        $device-silver-panel 100%
      );
      left: -10px;
    }
    &::before {
      background: radial-gradient(
        circle at bottom right,
        transparent 0,
        transparent 75%,
        $device-silver-panel 75%,
        $device-silver-panel 100%
      );
      right: -10px;
    }
  }

  .device-sensors {
    &::after,
    &::before {
      content: "";
      position: absolute;
    }
    &::after {
      background: #444;
      border-radius: 3px;
      height: 0.5vh;
      left: 50%;
      margin-left: -2.5vh;
      top: 2.25vh;
      width: 2.7vw;
    }
    &::before {
      background: #444;
      border-radius: 50%;
      height: 1.3vh;
      left: 50%;
      margin-left: 2vw;
      top: 1.8vh;
      width: 0.8vw;
    }
  }
  .device-btns-mute {
    background: $device-silver-dark;
    height: 3vh;
    left: -0.2vw;
    position: absolute;
    top: 10vh;
    width: 0.25vw;
  }
  .device-btns-mute:hover {
    background-color: #333;
  }

  .device-btns {
    background: $device-silver-dark;
    height: 5vh;
    left: -0.1vw;
    position: absolute;
    top: 16vh;
    width: 0.25vw;

    &::after {
      background: $device-silver-dark;
      content: "";
      height: 5vh;
      left: 0;
      position: absolute;
      top: 6.5vh;
      width: 0.25vw;
    }
  }

  .device-power {
    background: $device-silver-dark;
    height: 10vh;
    right: -0.2vw;
    position: absolute;
    top: 14vh;
    width: 0.25vw;
  }
  .device-power:hover {
    background-color: #333;
  }
}
