.bottomMenu {
  margin: auto;
  opacity: 1;
}
.bottomMenuActive {
  @extend .bottomMenu;
  color: #177ddc;
}
.bottomMenu:hover {
  opacity: 0.8;
}
.phoneNumber {
  height: 4.5vh;
  width: 100%;
  text-align: center;
}

.callButtons {
  height: 6vh;
  max-width: 3.5vw;
  padding: 0.5vh;
  margin: 0 auto;
  margin-top: 1vh;
  text-align: center;
  background-color: #343434;
  border-radius: 50%;
  opacity: 1;
}

.callButtons:hover {
  opacity: 0.8;
}

.callButtonAction {
  height: 6vh;
  width: 3.5vw;
  position: absolute;
  left: 38%;
  bottom: 5vh;
  padding: 0.5vh;
  margin: 1vh auto;
  text-align: center;
  border-radius: 50%;
  opacity: 1;
}

.callButtonAction:hover {
  opacity: 0.8;
}

.phoneCallRemoveDigit {
  position: absolute;
  bottom: 7.5vh;
  right: 2vw;
  opacity: 1;
}
.phoneCallRemoveDigit:hover {
  opacity: 0.8;
}

.addContact {
  display: inline;
  color: #177ddc;
  font-size: 1.5vh;
}
.addContact:hover {
  opacity: 0.8;
}
